import Aisde from "./aside";

interface IProps {
    aside?: "minimize";
    sidebarLayout?: 1 | 2;
    children: React.ReactNode;
    noMenu?: "Y" | "N";
}

const Layout = ({ children, aside, sidebarLayout, noMenu }: IProps) => {
    return (
        <>
            {noMenu !== "Y" && (
                <Aisde layout={aside} sidebarLayout={sidebarLayout} />
            )}

            {children}
        </>
    );
};

Layout.defaultProps = {
    sidebarLayout: 1,
};

export default Layout;
