import { useLocation } from 'react-router-dom';

const Logo = () => {
    const location = useLocation();
    console.log({ location });

    return (
        <>
            {location?.pathname === '/taxation' && (
                <>
                    <Styled2Logo path="/">
                        <>
                            지인<span>세무회계</span>
                        </>
                    </Styled2Logo>
                </>
            )}
            {location?.pathname !== '/taxation' && (
                <>
                    <StyledLogo path="/">
                        <>
                            CLUST<span>PARTNERS</span>
                        </>
                    </StyledLogo>
                </>
            )}
        </>
    );
};

export default Logo;
